<template>
	<div class="mobile-layout">
		<div class="custom-nav-bar">
			<div class="custom-nav-bar-content tn-flex tn-flex-col-center tn-flex-row-between">
				<div class="logo tn-margin-left-xs tn-flex-1 tn-flex tn-flex-col-center tn-flex-row-left">
					<img style="width: 32px;" src="@/assets/logo.svg">
				</div>
				<!-- <div class="wallet-connect">0xEBC6…13FB</div> -->
				<div class="lang-change tn-margin-right-xs">
					<div class=" tn-flex tn-flex-row-center" @click="showLangSelect">
						<span>{{ language }}</span>
					</div>
					<div class="language-popover" v-show="showLangList">
						<div class="lang-item" @click="selectLang('zh-cn')">中文</div>
						<div class="lang-item" @click="selectLang('en')">EN</div>
					</div>
				</div>
			</div>
		</div>
		<router-view></router-view>
		<van-tabbar class="tabbar-box dd-glass" v-model="active" inactive-color="#ffffff" active-color="#5CA2FF"  :fixed="true" route>
			<van-tabbar-item style="background-color: transparent;" v-for="(item, index) in tabbars" :key="index" replace :to="item.path">
				<span>{{ item.title }}</span>
				<template #icon="props">
					<!-- <img :src="item.active" /> -->
					<img :src="props.active ? item.active : item.inactive" />
				</template>
			</van-tabbar-item>

		</van-tabbar>
	</div>
</template>

<script>
import {
	mapState
} from 'vuex';
import icon1_ac from '@/assets/tabbar/icon1-ac.png';
import icon2_ac from '@/assets/tabbar/icon2-ac.png';
import icon3_ac from '@/assets/tabbar/icon3-ac.png';
import icon1 from '@/assets/tabbar/icon1.png';
import icon2 from '@/assets/tabbar/icon2.png';
import icon3 from '@/assets/tabbar/icon3.png';
export default {
	data: () => ({
		showLangList: false,
		lang: localStorage.getItem("lang"),
		tabbars: [
			{
				active: icon1_ac,
				inactive: icon1,
				title: '首页',
				path: '/home'
			},
			{
				active: icon2_ac,
				inactive: icon2,
				title: '朋友',
				path: '/friend'
			},
			{
				active: icon3_ac,
				inactive: icon3,
				title: '数据',
				path: '/data'
			}
		],
		active: 0
	}),
	computed: {
		...mapState(['walletAddress']),
		language() {
			if (!this.lang) {
				// eslint-disable-next-line vue/no-side-effects-in-computed-properties
				this.lang = this.$appConfig.defaultLang;
			}
			console.log(this.lang)
			return this.$appConfig.langConfig[this.lang];
		}
	},
	methods: {
		getIconPath(path) {
			return import(path);
		},
		showLangSelect() {
			this.showLangList = true;
		},
		selectLang(lang) {
			this.showLangList = false;
			if (this.lang == lang) return;
			window.localStorage.setItem("lang", lang);
			this.lang = lang;
			window.location.reload();
		}
	}
};
</script>

<style lang="scss">
@import "../styles/theme.scss";
@import "../styles/index.scss";
@import "../styles/mobile.scss";
@import "../styles/newUI.css";
